@import "../../variables.scss";

.footer {
  background-color: rgba(000, 000, 000, 0.5);
  height: 120px;
  position: absolute;
  bottom: 0;
  width: 100%;
  .footer-inner {
    .footer-content {
      padding: 19px 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 870px;
      width: 100%;
      margin: 0 auto;
      .footer-logo-block {
        max-width: 185px;
        width: 100%;
        .footer-logo {
          display: block;
          width: 60px;
          height: 60px;
          background-image: url("../../assets/humane&Healthy.png");
          background-size: 100%;
          background-repeat: no-repeat;
          a {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
      }

      .footer-nav {
        max-width: 500px;
        width: 100%;
        margin: 0;

        .footer-nav-link {
          max-width: 50px;
          width: 100%;
          margin: 0 auto;
          display: block;
          color: $white;
          text-decoration: none;
          transition: color 0.2s;
          -webkit-transition: color 0.2s linear;
          -moz-transition: color 0.2s linear;
          -ms-transition: color 0.2s linear;
          -o-transition: color 0.2s linear;
        }
        .footer-nav-link:hover {
          color: $lightGreen;
        }
      }
      .social {
        max-width: 185px;
        width: 100%;
        .social-inner {
          text-align: right;
          .social-link {
            color: $white;
            transition: color 0.2s;
            -webkit-transition: color 0.2s linear;
            -moz-transition: color 0.2s linear;
            -ms-transition: color 0.2s linear;
            -o-transition: color 0.2s linear;
          }
          .social-link:hover{
            color: $lightGreen;
          }
        }
      }
    }
    .copyright {
      font-family: $secondaryTextFF;
      color: #fff;
      font-size: 10px;
      margin: 0 auto;
      width: 100%;
      max-width: 400px;
    }
  }
}
.footer.display-block {
  display: block;
}
.footer.display-none {
  display: none;
}

@media only screen and (max-width: 768px){
  .footer {
    display: none!important;
  }
}